.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
  -webkit-filter: invert(100%);
          filter: invert(100%);
}

.av-logo{
  display: block;
  margin: 0 auto;
  width: 200px;
}

.App-header {
  background: -webkit-gradient(linear,left top, left bottom,from(#23253e),to(#21254d));
  background: -webkit-linear-gradient(#23253e,#21254d);
  background: -o-linear-gradient(#23253e,#21254d);
  background: linear-gradient(#23253e,#21254d);
  padding: 20px;
  color: black;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

body{
  background: #E6E8E6;
}


